import { Component, OnInit } from '@angular/core';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { NavListEnum } from './navList-enum';
@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.styl'],
})
export class NavComponent implements OnInit {
  NavListEnum = NavListEnum;
  isVisible = false;
  navName?: string;
  navUrl?: string;
  dataIndex = 0;
  constructor() {}

  // tslint:disable-next-line:typedef
  drop(event: CdkDragDrop<string[]>, index: number) {
    moveItemInArray(
      NavListEnum[index].data,
      event.previousIndex,
      event.currentIndex
    );
  }
  ngOnInit(): void {
    this.getData();
  }
  saveData(): void {
    window.localStorage.setItem('navList', JSON.stringify(this.NavListEnum));
  }
  getData(): void {
    try {
      this.NavListEnum = JSON.parse(
        window.localStorage.getItem('navList') || ''
      );
    } catch (error) {
      console.warn(error);
    }
  }
  showModal(index: number): void {
    this.isVisible = true;
    this.dataIndex = index;
  }
  handleOk(): void {
    console.log('Button ok clicked!');
    this.isVisible = false;
    this.NavListEnum[this.dataIndex].data.push({
      name: this.navName,
      url: this.navUrl,
    });
    this.saveData();
  }

  handleCancel(): void {
    console.log('Button cancel clicked!');
    this.isVisible = false;
  }
}
